import React from 'react';
import styles from './EntregasFilter.module.scss';
import { TiTime } from 'react-icons/ti';
import asignaturas from './../../../../dummyData/asignaturas';

const EntregasFilter = ({ windowContext }) => {
    const AsignaturasIcon = windowContext.windows.asignaturas.icon;

    const selectOptions = {
        asignaturas: [
            ...asignaturas.map(asignatura => {
                return {
                    value: asignatura.id,
                    label: asignatura.name
                };
            })
        ],

        plazo: [
            {
                value: 'estaSemana',
                label: 'Para esta semana'
            },
            {
                value: 'proximaSemana',
                label: 'Para la proxima semana'
            }
        ]
    };

    const filterEntregas = (event) => {
        console.log(event.target.value);
    }

    return (
        <div className={styles.entregasFilters}>
            <span className={styles.entregasFiltersText}>Ordenar por:</span>

            <div className={styles.sortBy}>

                <div>
                    <div className={styles.sortByText}>
                        <AsignaturasIcon className={styles.sortByIcon} />
                        <span>Asignatura:</span>
                    </div>

                    <div>
                        <select
                            className={styles.sortBySelect}
                            onChange={(event) => filterEntregas(event)}
                        >
                            {
                                selectOptions.asignaturas.map(option => (
                                    <option
                                        value={option.value}
                                        key={option.value}
                                    >
                                        {option.label}
                                    </option>
                                ))
                            }
                        </select>
                    </div>
                </div>

                <div>
                    <div className={styles.sortByText}>
                        <TiTime className={styles.sortByIcon} />
                        <span>Plazo:</span>
                    </div>

                    <div>
                        <select className={styles.sortBySelect}>
                            {
                                selectOptions.plazo.map(option => (
                                    <option
                                        value={option.value}
                                        key={option.value}
                                    >
                                        {option.label}
                                    </option>
                                ))
                            }
                        </select>
                    </div>
                </div>

            </div>
        </div>
    );
};

export default EntregasFilter;